
import { Vue, Options } from "vue-class-component";
import { MessageType } from "@/database/dos/ChatRecord";
import { ChatModule } from '@/store/modules/chat/chat'
import { ChatCtrl } from './ChatCtrl'
@Options({
  name: "ChatBottom",
  props: {
    name: { type: String },

  }
})
export default class extends Vue {
  public $refs!: { input: HTMLFormElement };
  private text = "";
  private voiceMode = false;
  private showSendButton = false;
  private chats = require("@/data/chats.json");

  switchVoiceMode() {
    this.voiceMode = !this.voiceMode;
    if (!this.voiceMode) {
      // todo 开启后聚焦到input
    }
  }
  input(e) {
    this.showSendButton = this.text.length < 1;
    // console.log("对方正在输入" + this.showSendButton);
  }
  send(e) {
    if (this.text != "") {
      ChatModule.addChatRecord({
        isMe: true,
        photo: "",
        type: MessageType.text,
        content: this.text,
      })
    }
    
    this.sendMsg(this.text)
    this.text = ""
  }

  private sendMsg(text:string){
    ChatModule.setHead({
      name: "对方正在输入...",
      img: null,
      id: null
    })
    let chatCtr = ChatCtrl.getChatCtrl(ChatModule.currentConversion);
    return chatCtr.sendMsg(text)
      .then(res => {
        ChatModule.setHead({
              name: this.chats[ChatModule.currentConversion]["name"],
              img: null,
              id: null
          })

        let originText = res.data.choices[0].text
        
        if(originText == undefined) {
          originText = "..."
        }
        let split = originText.split("：")

        let text = ""
        if(split.length == 2) {
          text = originText == undefined ? "" : split[1]
        } else{
          text = originText
          originText = this.chats[ChatModule.currentConversion]["call"] +"："+originText
        }
        ChatModule.addChatRecord({
          isMe: false,
          photo: ChatModule.img,
          type: MessageType.text,
          content: text
        })
        if (originText != undefined) {
          chatCtr.addContent(originText)
        }
        
        console.log(res.data.choices[0]["finish_reason"])
        if(res.data.choices[0]["finish_reason"] != "stop") {
          return this.sendMsg("")
        }

      }).finally(() => {
        ChatModule.setHead({
          name: this.chats[ChatModule.currentConversion]["name"],
          img: null,
          id: null
        })
      })
  }
}
