import { CreateCompletionRequest, Configuration, OpenAIApi } from 'openai'
import { Buffer } from 'buffer';


export class ChatGptUtils {
    private static configuration = new Configuration({
      apiKey: Buffer.from('c2stb1Nab0txb3NOTXpIOXFNSlF6VGhUM0JsYmtGSjZuNDRxWmk3ZUdkWnNGa1RaNGM0', 'base64').toString('ascii')
      });

    private static openai = new OpenAIApi(ChatGptUtils.configuration);
    
    public static request(req:CreateCompletionRequest) {
      return ChatGptUtils.openai.createCompletion(req)
    }
    public static requestTemp(msg:string){
        return ChatGptUtils.openai.createCompletion({
            model: "text-davinci-003",
            prompt: msg,
            temperature: 0.9,
            max_tokens: 150,
            top_p: 1,
            frequency_penalty: 0.0,
            presence_penalty: 0.6,
            stop: [" Human:", " AI:"],
          })
    }
}

