import { ChatGptUtils } from '@/utils/ChatGPT'

export class ChatCtrl {
    private static chatList:ChatCtrl[] = [];
    private context:string|undefined;
    private prev:string|undefined;

    private constructor(init: string) {
        this.context = init;
    }
    public static getChatCtrl(id:number):ChatCtrl {
        let chats = require("@/data/chats.json");
        if(ChatCtrl.chatList[id] == undefined) {
            ChatCtrl.chatList[id] = new ChatCtrl(chats[id].init);
        }
        return this.chatList[id]
    }

    public sendMsg(msg: string) {
        // console.log("send msg:" + msg)
        if(msg != ""){
            this.context += `|用户：${msg}|`
        }
        
        return ChatGptUtils.request({
            model:"text-davinci-003",
            prompt: this.context,
            temperature: 0.5,
            max_tokens:60,
            top_p:0.3,
            frequency_penalty:0.5,
            presence_penalty:0,
            stop:["|"]
        })
    }

    public addContent(content:string|undefined){
        this.context += content == undefined?"":content;
        // console.log("content:::"+this.context)
    }
}
